import * as TYPE from "../action-types";
import { Status } from "rc-steps/lib/interface";
// import { IQuesData } from "../../views/examination";
const wait: Status = "wait";
const finish: Status = "finish";
export interface IStep {
  current?: number;
  title?: string;
  status?: Status;
}
interface IQuesDataOrigin {
  [key: string]: string;
}
interface IState {
  questionListOrigin: IQuesDataOrigin[];
  questionList: IQuesData[];
  current: number;
  steps: IStep[];
  question_focus: number;
  focus_msg: object | undefined;
  exam_id: number;
  examStatus: boolean;
  login_id: number; // 登录用户id
  examHostLink: string; // 答题系统域名
  ques_type: string;
  hiddenHolder: IQuesData[];
  showSerial: boolean;
  typeList: ITypeList;
  question_store: IQuestionOriginItem[];
  focusMSG: {
    id: number;
    question_name?: string;
    question_type?: string;
    question_score?: number;
    answer?: string;
    option_list: IOptionItem[];
  };
}
const steps: IStep[] = [
  { current: 0, title: "编辑项目", status: wait },
  { current: 1, title: "发布项目", status: wait },
  { current: 2, title: "数据报表", status: wait },
];
interface IAction {
  type: string;
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  payload: any;
}
const initState: IState = {
  questionListOrigin: [],
  questionList: [],
  current: 0,
  steps: steps,
  question_focus: -1,
  focus_msg: {},
  exam_id: -1,
  examStatus: false,
  login_id: 2, // 登录用户id
  examHostLink: "http://192.168.5.104:5008/vue.html#/studentInfo",
  ques_type: "",
  hiddenHolder: [],
  showSerial: true,
  typeList:[],
  question_store:[],
  focusMSG: {
    id: -1,
    question_name: "",
    question_type: "",
    question_score: 0,
    answer: "",
    option_list: [],
  },
};
const local = localStorage.getItem("examEdit");
let localState: IState;
if (local) {
  localState = JSON.parse(local);
}
function Reducer(state = localState || initState, action: IAction) {
  state = JSON.parse(JSON.stringify(state));
  const { type, payload } = action;
  let question_temp, payloadCopy, questionItem;
  switch (type) {
  case TYPE.SETORIGINLIST:
    state.questionListOrigin = payload.questionList;
    state.questionList = payload.questionList.map(
      (question: IQuesDataOrigin) => {
        const {
          id,
          creator_id,
          question_name,
          question_image,
          question_score,
          question_type,
          id_in_store,
          answer,
          store_type,
        } = question;
        const option_list = Object.keys(question)
          .filter(
            (item) =>
              item.indexOf("option") !== -1 &&
                question[item] !== null &&
                item.indexOf("image") === -1
          )
          .map((item) => ({
            option_name: question[item],
            option_image: question[item + "_image"],
          }));
        return {
          id,
          creator_id,
          question_name,
          question_image,
          question_score,
          question_type,
          id_in_store,
          answer,
          store_type,
          option_list,
        };
      }
    );
    state.hiddenHolder = JSON.parse(JSON.stringify(state.questionList));
    break;
  case TYPE.SETSTEPSTATUE:
    state.steps.forEach((item: IStep) => (item.status = wait));
    state.steps[payload.index].status = finish;
    state.current = payload.index;
    break;
  case TYPE.SETQUESFOCUS:
    state.question_focus = payload.index;
    state.focus_msg = state.questionList.find(
      (item: IQuesData) => item.id === state.question_focus
    );
    break;
  case TYPE.SETQUESTYPE:
    state.ques_type = payload.ques_type;
    break;
  case TYPE.SETEXAMID:
    state.exam_id = payload.exam_id;
    break;
  case TYPE.UPDATEQUESLIST:
    question_temp = state.questionList.find(
      (item: IQuesData) => item.id === +payload.id
    );
    if (+payload.index === -1) {
      if (question_temp) {
        question_temp.question_image = payload.imgUrl;
      }
    } else {
      if (question_temp) {
        question_temp.option_list[payload.index].option_image =
            payload.imgUrl;
      }
    }
    break;
  case TYPE.SETFOCUSMSG:
    payloadCopy = JSON.parse(JSON.stringify(payload));
    // console.log(payloadCopy.option_list);    
    state.focusMSG.id = payloadCopy.id;
    state.focusMSG.answer = payloadCopy.answer;
    state.focusMSG.question_name = payloadCopy.question_name;
    state.focusMSG.question_score = payloadCopy.question_score;
    state.focusMSG.question_type = payloadCopy.question_type;
    state.focusMSG.option_list = payloadCopy.option_list;
    questionItem =
        state.questionList.find((item) => item.id === payload.id) ||
        state.questionList[0];
    questionItem.option_list = payloadCopy.option_list;
    questionItem.question_score = payloadCopy.question_score;
    break;
  case TYPE.SETHIDDENHOLDER:
    state.hiddenHolder = JSON.parse(JSON.stringify(payload.hiddenHolder));
    break;
  case TYPE.CHANGESHOWSERIAL:
    state.showSerial = payload.showSerial;
    break;
  case TYPE.SETLOGINID:
    state.login_id = payload.id;
    break;
  case TYPE.SETTYPELIST:
    state.typeList=payload.typeList;
    break;
  case TYPE.SETQUESTIONSTORE:
    state.question_store=payload.question_store;
    break;
  case TYPE.SETEXAMSTATUS:
    state.examStatus=payload.examStatus;
    break;
  default:
  }
  // console.log(state.hiddenHolder);
  return state;
}

export default Reducer;
