import * as TYPE from "../action-types";
const initState = {
  count: 0,
  status:0
};
interface IPayload{
  status:number;
}
interface IAction {
  type: string;
  payload: any;
}
function Reducer(state = initState, action: IAction) {
  state = JSON.parse(JSON.stringify(state));
  switch (action.type) {
  case TYPE.DEMO:
    state.count++;
    state.status--;
    console.log(action,state.status);
    break;
  default:
  }
  return state;
}

export default Reducer;
