import React, { Suspense, FC } from "react";
import {
  Switch,
  Route,
  Redirect,
  withRouter,
  NavLink,
  RouteComponentProps,
} from "react-router-dom";

interface IProps extends RouteComponentProps {
  routes: Array<IRoute>;
  redirect?: string;
}
// type Token = string | null;
const RouterView: FC<IProps> = (props) => {
  // console.log(props);
  // async function check(token: Token) {
  //   const res = await loginCheck({ token });
  //   if (res.code === 401) {
  //     props.history.push("/login");
  //   }
  //   if (res.code === 200) {
  //     if (
  //       props.location.pathname === "/login" ||
  //       props.location.pathname === "/"
  //     ) {
  //       props.history.push("/mine");
  //     }
  //   }
  // }
  // const token: Token = localStorage.getItem("token");
  // useEffect(() => {
  //   if (!token) {
  //     props.history.push("/login");
  //   } else {
  //     check(token);
  //   }
  // }, []);
  return (
    <Suspense fallback="">
      <Switch>
        {props.routes.map((route) => (
          <Route
            key={route.path}
            path={route.path}
            component={(props: JSX.IntrinsicAttributes) => {
              return (
                <route.component
                  {...props}
                  path={route.path}
                  routes={route.routes}
                />
              );
            }}
          />
        ))}
        {props.redirect ? <Redirect to={props.redirect} /> : ""}
      </Switch>
    </Suspense>
  );
};
export default withRouter(RouterView);

const _RouterLink: FC<IProps> = (props) => {
  return (
    <div className="navBox">
      {props.routes.map((item: IRoute) => (
        <NavLink key={item.path} to={item.path} className="navLink">
          <span
            className={"iconfont " + (item.meta ? item.meta.icon : "")}
          ></span>
          <span>{item.meta ? item.meta.title : ""}</span>
        </NavLink>
      ))}
    </div>
  );
};
export const RouterLink = withRouter(_RouterLink);
