import React, { FC } from "react";
import { Provider } from "react-redux";
import store from "./store";
import RouterView from "./route/index";
import routes from "./route/router.config";
import "./App.less";

const App: FC = () => {
  return (
    <Provider store={store}>
      <div className="App">
        <RouterView routes={routes} redirect="/login" />
      </div>
    </Provider>
  );
};
// window.onbeforeunload=()=>{
//   localStorage.setItem("examEdit",JSON.stringify(store.getState().examEdit));
// };
window.addEventListener('beforeunload',()=>{
  localStorage.setItem("examEdit",JSON.stringify(store.getState().examEdit));
});
export default App;
